body {
  margin: 0;
  font-family: 'Gilroy', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
}

ul {
  list-style: none;
}

button {
  font-family: inherit;
}

.sp-force-hide {
  display: none;
}
.sp-form[sp-id='227301'] {
  display: block;
  background: rgba(240, 240, 240, 1);
  padding: 30px;
  width: 350px;
  max-width: 100%;
  border-radius: 10px;
  font-family: Tahoma, Geneva, sans-serif;
  background-repeat: no-repeat;
  background-position: center;
  background-size: auto;
}
.sp-form[sp-id='227301'] input[type='checkbox'] {
  display: inline-block;
  opacity: 1;
  visibility: visible;
}
.sp-form[sp-id='227301'] .sp-form-fields-wrapper {
  margin: 0 auto;
  width: 290px;
}
.sp-form[sp-id='227301'] .sp-form-control {
  background: #ffffff;
  border-color: CCCCCC;
  border-style: solid;
  border-width: 1px;
  font-size: 15px;
  padding-left: 8.75px;
  padding-right: 8.75px;
  border-radius: 5px;
  height: 35px;
  width: 100%;
}
.sp-form[sp-id='227301'] .sp-field label {
  color: rgba(0, 0, 0, 1);
  font-size: 14px;
  font-style: normal;
  font-weight: normal;
}
.sp-form[sp-id='227301'] .sp-button-messengers {
  border-radius: 5px;
}
.sp-form[sp-id='227301'] .sp-button {
  border-radius: 5px;
  background-color: #fe520b;
  color: #ffffff;
  width: 100%;
  font-weight: bold;
  font-style: normal;
  font-family: Tahoma, Geneva, sans-serif;
  box-shadow: none;
}
.sp-form[sp-id='227301'] .sp-button-container {
  text-align: center;
  width: auto;
}

.swiper-pagination-bullet {
  width: 12px !important;
  height: 12px !important;
  border-radius: 50%;
  border: 1px solid red !important;
  background: transparent !important;
  opacity: 1 !important;
}
.swiper-pagination-bullet-active {
  background: red !important;
}

.swiper-button-prev:after {
  content: '' !important;
  position: absolute;
  width: 65px;
  height: 65px;
  background-image: url('./img/arrow.svg');
  top: -36px;
  left: -64px;
}

.swiper-button-next:after {
  content: '' !important;
  position: absolute;
  width: 65px;
  height: 65px;
  background-image: url('./img/arrow.svg');
  transform: rotate(180deg);
  top: -36px;
  right: -64px;
}

.swiper {
  /* width: 100vw !important; */
  overflow: visible !important;
}

@media screen and (max-width: 1000px) {
  .swiper-button-prev:after {
    top: -36px;
    left: 0px;
  }

  .swiper-button-next:after {
    top: -36px;
    right: 0px;
  }
}
@media screen and (max-width: 768px) {
  .swiper-button-prev:after {
    width: 0;
    height: 0;
  }

  .swiper-button-next:after {
    width: 0;
    height: 0;
  }

  .swiper-pagination-bullet {
    width: 8px !important;
    height: 8px !important;
  }
}
@media screen and (max-width: 425px) {
  .sp-form[sp-id='227301'] {
    padding: 20px;
    width: 350px;
    max-width: 100%;
  }
}

.PhoneInputInput {
  border: none;
  outline: none;
  background-color: white;
  font-weight: 500;
  font-size: 15px;
  line-height: calc(23 / 15);
  letter-spacing: -4%;
}
.PhoneInput {
  display: flex;
  gap: 9px;
  padding: 9px;
  border: 1px solid grey;

  width: 100%;
  font-weight: 500;
  font-size: 15px;
  line-height: calc(23 / 15);
  letter-spacing: -4%;
  margin-bottom: 16px;
  margin-top: 4px;
}

.PhoneInputError {
  border: 1px solid #ee4f4f;
}

.PhoneInput:focus,
.PhoneInput:focus-within {
  border: 1px solid green;
}

.PhoneInputCountry {
  width: 28px;
  height: 14px;
  margin-right: 0;
  margin-top: 4px;
}

.PhoneInputCountryIcon--border {
  box-shadow: none;
}
