.title {
  max-width: 500px;
}
.title strong {
  color: #fe520b;
}
.title h1 {
  font-size: 64px;
  line-height: 64px;
  letter-spacing: -1.28px;
  color: #fff;
  margin-bottom: 16px;
}
.title p {
  font-weight: 600;
  font-size: 26px;
  line-height: 29px;
  letter-spacing: -0.52px;
  color: #fff;
  margin-bottom: 62px;
}

.hero {
  max-width: 100%;
  background-color: #0c0b31;
  position: relative;
  overflow: hidden;
}

.blurImg {
  position: absolute;
  top: 0;
  right: 0;
}

.container {
  max-width: 1440px;
  margin: 0 auto;
  padding: 68px 140px 180px 147px;
  position: relative;
  display: flex;
  justify-content: space-between;
}

.btn {
  font-size: 16px;
  line-height: 18px;
  letter-spacing: -0.16px;
  color: #fff;
  background-color: #fe520b;
  padding: 25px 60px;
  border-radius: 10px;
  margin-bottom: 12px;
  text-transform: uppercase;
  border: none;
  outline: none;
  opacity: 1;
  transition: all 0.3s ease-in-out;
  cursor: pointer;
  text-decoration: none;
  display: inline-block;
}

.btn:hover {
  background-color: #d24308;
}

.desc {
  font-size: 14px;
  line-height: 15px;
  letter-spacing: -0.42px;
  text-transform: uppercase;
  font-weight: 600;
  color: #fff;
  margin-left: 62px;
}

.img {
  position: absolute;
  /* left: 623px; */
  left: 43%;
  bottom: -30px;
}

.blurImgTab,
.blurImgMob {
  display: none;
}

.suptitle {
  /* margin-bottom: 33px; */
  display: flex;
}
.suptitle p {
  color: #131033;
  font-size: 16px;
  line-height: 18px;
  font-weight: 600;
  letter-spacing: -0.64px;
  margin: 0;
  padding: 10px 16px;
  background-color: #fff;
  max-width: 193px;
  border-radius: 10px;
  /* margin-right: 24px; */
}

.containerBox {
  display: flex;
  gap: 24px;
  align-items: center;
}

.dateBox {
  display: flex;
  gap: 15px;
}

.time,
.date {
  padding: 0;
  background-color: none;
  margin: 0;
  display: flex;
  gap: 10px;
  align-items: center;
  font-weight: 500;
  font-size: 16px;
  line-height: 18px;
  letter-spacing: -0.48px;
  color: #fff;
}

.suptitle strong {
  color: #fe520b;
}

.block {
  padding: 22px 45px 37px;
  background-color: #e9e7f9;
  border-radius: 15px 15px 15px 0;
  max-width: 300px;
  height: max-content;
  margin-top: 52px;
  position: relative;
  z-index: 2;
}

.blockTitle {
  font-size: 24px;
  line-height: 36px;
  color: #0a2032;
  margin-bottom: 16px;
  margin-top: 0;
}
.blockDesc {
  font-size: 15px;
  line-height: 21px;
  color: #0a2032;
  margin-bottom: 24px;
  max-width: 135px;
}
.item {
  font-size: 15px;
  line-height: 21px;
  color: #fff;
  padding: 6px 15px;
  background-color: #191535;
  border-radius: 2px;
  display: inline-block;
  max-width: 197px;
  margin-top: 0;
}
.item2 {
  max-width: 140px;
}
.item:not(:last-child) {
  margin-bottom: 6px;
}

@media screen and (max-width: 1300px) {
  .container {
    padding: 68px 80px 180px 80px;
  }
}
@media screen and (max-width: 1245px) {
  .block {
    display: none;
  }
  .img {
    right: 54px;
    left: auto;
  }
  .blurImg {
    display: none;
  }
  .blurImgTab {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
  }
}
@media screen and (max-width: 1000px) {
  .container {
    padding: 50px 40px 130px 40px;
  }
  .img {
    right: 0;
  }
  .img img {
    width: 355px;
    height: 580px;
  }
}
@media screen and (max-width: 846px) {
  .container {
    padding: 50px 20px 130px;
  }
  .img {
    right: -34px;
  }
  .img img {
    width: 355px;
    height: 580px;
  }
}
@media screen and (max-width: 768px) {
  .container {
    padding: 25px 13px 33px;
    flex-direction: column;
    /* justify-content: center; */
  }
  .blurImgTab {
    display: none;
  }
  .blurImgMob {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    width: 100vw;
  }
  .img {
    left: calc(50% - 120px);
    top: 39%;
    right: auto;
    z-index: 0;
  }
  .img img {
    width: 241px;
    height: 394px;
  }
  .containerBox {
    flex-direction: column-reverse;
    gap: 22px;
  }
  .time,
  .date {
    font-size: 12px;
    line-height: 13px;
    gap: 6px;
  }
  .date img {
    width: 13px;
    height: 13px;
  }
  .time img {
    width: 14px;
    height: 14px;
  }
  .suptitle p {
    font-size: 14px;
    line-height: 15px;
  }
  .title {
    text-align: center;
    margin: 0 auto;
  }
  .title h1 {
    font-size: 40px;
    line-height: 40px;
    margin-bottom: 12px;
    margin-top: 15px;
  }
  .title p {
    font-size: 16px;
    line-height: 21px;
    max-width: 298px;
    margin: 0 auto;
    margin-bottom: 355px;
  }
  .btn {
    max-width: 314px;
    font-size: 14px;
    line-height: 15px;
    position: relative;
    z-index: 5;
  }
  .containerBtnMob {
    text-align: center;
  }
}

