/* form */
.form {
  padding: 30px 40px 24px;
  background-color: #f0f0f0;
  border-radius: 16px;
  width: 376px;
  position: relative;
  z-index: 50;
}

.formLabel {
  display: block;
  font-size: 12px;
  line-height: calc(18 / 12);
  color: black;
}
.formInput {
  /* display: block; */
  padding: 9px;
  border: 1px solid #999999;
  width: 100%;
  font-weight: 500;
  font-size: 15px;
  line-height: calc(23 / 15);
  letter-spacing: -4%;
  margin-bottom: 16px;
  margin-top: 4px;
  background-color: white;
  outline: none;
  border-radius: 8px;
}

.formInput:focus {
  border: 1px solid yellow;
  background-color: white;
}

.formInputError {
  border: 1px solid #ee4f4f;
}

.form-error {
  font-size: 12px;
  line-height: calc(18 / 12);
  color: #ee4f4f;
  margin-top: -14px;
}
.formRadioGroup {
  display: flex;
  gap: 9px;
  margin-top: 8px;
  justify-content: space-between;
}

.formLink {
  font-size: 12px;
  line-height: calc(18 / 12);
  text-align: center;
  color: black;
  max-width: 292px;
}

.formLinkUnder {
  text-decoration: underline;
}

.PhoneInput {
  display: flex;
  gap: 9px;
  padding: 9px;
  border: 1px solid grey;

  width: 100%;
  font-weight: 500;
  font-size: 15px;
  line-height: calc(23 / 15);
  letter-spacing: -4%;
  margin-bottom: 16px;
  margin-top: 4px;
}

.PhoneInputError {
  border: 1px solid #ee4f4f;
}

.PhoneInput:focus,
.PhoneInput:focus-within {
  border: 1px solid green;
}

.PhoneInputCountry {
  width: 28px;
  height: 14px;
  margin-right: 0;
  margin-top: 4px;
}

.PhoneInputCountryIcon--border {
  box-shadow: none;
}

.PhoneInputInput {
  border: none;
  outline: none;
  background-color: white;
  font-weight: 500;
  font-size: 15px;
  line-height: calc(23 / 15);
  letter-spacing: -4%;
}

.btnForm {
  padding: 25px 50px;
}

.formLink {
  color: #171717;
  font-style: 10px;
  line-height: 13px;
  text-align: center;
}

.formLinkUnder {
  text-decoration: underline;
}

.formTitle {
  font-weight: 700;
  font-size: 26px;
  line-height: 27px;
  text-align: center;
  color: #0a0a0a;
}
.formTitle span {
  color: #fe520b;
}
